

<script>
import adminService from './adminService.js';
import axios from 'axios';
import Vue from 'vue'
import Toaster from 'v-toaster'
import 'v-toaster/dist/v-toaster.css'
Vue.use(Toaster)

import { APIENDPOINT , getHeader, token } from '../../app.config';

export default {
  template:require('./admin.html'),
  data() {
    return {
      adminDetails : {
        message : ''
      },
      el: '#admin',
      posts : null,

    }
  },

  created: function(){
    this.admin()
  },
  methods: {

    admin:function() {
      const sessionId = JSON.parse(window.localStorage.getItem('lbUser')).sessionid;
      const token = JSON.parse(window.localStorage.getItem('lbUser')).token;

      axios.get(APIENDPOINT + '/post/newsFeed', {
        headers : {
          'Authorization':token
        },
        withCredentials: true
      })
      .then((response) => {
        const data = response.data;
        this.posts = data.posts;
      })
      .catch((err) => {
        console.error(err.stack || err);
      });
    },

    newPublication:function() {
      const sessionId = JSON.parse(window.localStorage.getItem('lbUser')).sessionid;
      const token = JSON.parse(window.localStorage.getItem('lbUser')).token;

      axios({
        method:'put',
        url:APIENDPOINT + '/post/newPost',
        data: {"message": this.adminDetails.message},
        headers : {
                 'Authorization':token
                 },
        withCredentials: true
      })
      .then((response) => {
        const data = response.data;
        this.posts = data.posts;
        this.admin();
      })
      .catch((err) => {
        console.error(err.stack || err);
      });
    },

    openUser:function(idUserToWatch) {
      var app = this;
      var userConnected = JSON.parse(window.localStorage.getItem('lbUser')).data;
      if (userConnected.id === idUserToWatch) {
        app.$router.push('/profil');
      } else {
        const userId = idUserToWatch;
        app.$router.push({ name: 'profilUser', params: { userId }});
      }
    }
  }
}

</script>

<style lang="scss">
@import './admin.scss';
</style>



