

<script>
import suggestionService from './profilUserService.js';
import axios from 'axios';
import Vue from 'vue'
import Vuetify from 'vuetify'
Vue.use(Vuetify)

import { APIENDPOINT , getHeader, token } from '../../app.config';

export default {
  template:require('./profilUser.html'),
  data() {
    return {
      btnAction : {
        title: ''
      },
      toast : {
        snackbar: false,
        color: 'red',
        mode: '',
        timeout: 6000,
        text:''
      },
      userProfil : {
        nomInvocateur: 'test',
        poste: 'fdfjsdfjslk'
      },
      el: '#profilUser',
      profilUser : null,
    }
  },

  beforeRouteEnter (to, from, next) {
    next(vm => vm.setData(to.params.userId))
  },

  created: function(){

  },
  methods: {
    setData (userId) {
      this.findUser(userId);
    },

    findUser:function(userId) {
      const sessionId = JSON.parse(window.localStorage.getItem('lbUser')).sessionid;
      const token = JSON.parse(window.localStorage.getItem('lbUser')).token;
      var app = this;
      axios({
        method:'POST',
        url:APIENDPOINT + '/user/watchProfil',
        data: {"id": userId},
        headers : {
          'Authorization':token
        },
        withCredentials: true
      })
      .then((response) => {
        if(response.data.tRetour.codeRetour === "0000000000") {
            const data = response.data;
            console.log(data);
            this.userProfil.nomInvocateur = data.user.nomInvocateur;
            this.userProfil.poste = data.user.poste;
            console.log(data.user.enAmi);
            if (data.user.statusAmitie === "ACTIF") {
              this.btnAction.title = "Retirer de la liste d'ami";
            } else if (data.user.statusAmitie === "ATTENTE") {
              this.btnAction.title = "Annuler la demande d'ami";
            } else {
              this.btnAction.title = "Ajouter en ami";
            }
            this.toast.text = response.data.tRetour.messageRetour;
            this.toast.color='green';
            this.toast.snackbar=true;
          } else {
            this.toast.text = response.data.tRetour.messageRetour;
            this.toast.color='red';
            this.toast.snackbar=true;
          }
      })
      .catch((err) => {
        console.error(err.stack || err);
      });
    }
  }
}

</script>

<style lang="scss">
@import './profilUser.scss';
</style>



