

<script>
import profilService from './profilService.js';
import axios from 'axios';
import Vue from 'vue'
import Toaster from 'v-toaster'
import 'v-toaster/dist/v-toaster.css'
Vue.use(Toaster)

import { APIENDPOINT , getHeader, token } from '../../app.config';

export default {
  template:require('./profil.html'),
  data() {
    return {
      profilDetails : {
        username : '',
        password : '',
        role : '',
        nomInvocateur : '',
        email : '',
        password : ''
      },
      el: '#profil',
      posts : null,

    }
  },

  created: function(){
    this.profil()
  },
  methods: {

    profil:function() {
      const sessionId = JSON.parse(window.localStorage.getItem('lbUser')).sessionid;
      const token = JSON.parse(window.localStorage.getItem('lbUser')).token;

      axios.get(APIENDPOINT + '/user/myaccount', {
        headers : {
          'Authorization':token
        },
        withCredentials: true
      })
      .then((response) => {
        const data = response.data;
        this.profilDetails.username = data.userDto.login;
        this.profilDetails.nomInvocateur = data.userDto.nomInvocateur;
        this.profilDetails.email = data.userDto.mail;
        this.profilDetails.role = data.userDto.poste;
      })
      .catch((err) => {
        console.error(err.stack || err);
      });
    },

    newPublication:function() {
      const sessionId = JSON.parse(window.localStorage.getItem('lbUser')).sessionid;
      const token = JSON.parse(window.localStorage.getItem('lbUser')).token;

      axios({
        method:'put',
        url:APIENDPOINT + '/post/newPost',
        data: {"message": this.adminDetails.message},
        headers : {
                 'Authorization':token
                 },
        withCredentials: true
      })
      .then((response) => {
        const data = response.data;
        this.posts = data.posts;
      })
      .catch((err) => {
        console.error(err.stack || err);
      });
    }
  }
}

</script>

<style lang="scss">
@import './profil.scss';
</style>



